<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/practitioner/' + queries.practitioner)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-row>
      <v-col>
        <div v-for="role in roles" :key="role">
          <bw-page-view :id="role" page="practitioner-role" />
          <v-divider></v-divider>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      overlayUnclosed: false,
      loading: false,
      roles: []
    }
  },
  methods: {
    getPositions() {
      fetch("/fhir/PractitionerRole?_count=200&_sort=-date&practitioner=Practitioner/" + this.queries.practitioner).then(response => response.json().then(async(roles) => {
        for(let role of roles.entry) {
          if(this.roles.length) {
            setTimeout(() => {
              this.roles.push(role.resource.id)
            }, 600);
          } else {
            this.roles.push(role.resource.id)
          }
        }
        console.log(this.roles);
      }))
    }
  },
  components: {
    "bw-page-view": () => import("@/site/view/bw-page-view")
  },
  created() {
    this.getPositions()
  }
}
</script>